<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataAkreditasi"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataAkreditasi.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
	    <template v-slot:item.daya_tampung="{item}">
	    	{{ parseInt(item.daya_tampung.value)+'%' }}
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditAkreditasi :data="item" />
				<HapusAkreditasi :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditAkreditasi from '@/components/Akreditasi/EditAkreditasi'
	import HapusAkreditasi from '@/components/Akreditasi/HapusAkreditasi'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				{ text: 'Daya Tampung', value: 'daya_tampung', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					id: 1,
					nama: 'super',
					daya_tampung: 100
				}
			],
		}),

		components: {
			Loader,
			HapusAkreditasi,
			EditAkreditasi
		},

		created() {
			this.$store.dispatch('dataAkreditasi/getDataAkreditasi')
			this.$store.dispatch('dataAkreditasi/updateIsLoading', true)
		},

		computed: { ...mapState('dataAkreditasi', ['dataAkreditasi', 'isLoading']) }
	}
</script>